	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"/static/SourceGame.jpg\"></p>\n<p>Game Jam Vancouver 2016 - Source!</p>\n<p>This was my second time at the Vancouver Game Jam.\nOur team was able to make a very simple platformer game in the span of 48 hours.\nMost of the team's time was spent designing levels and creating custom 3D art assets for the environment and interactable objects.\nAs a result, the game was visually appealing, but the game's mechanics were that of a generic platformer (e.g. dodge fireballs and jump to reach platforms).</p>\n<p><strong>Link:</strong><br>\nThe game can be found on our Global Game Jam 2016 page: <a href=\"https://globalgamejam.org/2016/games/source\">Source</a>.</p>\n<p><strong>Team:</strong><br>\nThe team was made up of 7 people (3 developers, 4 artists). </p>\n<p><strong>Work:</strong><br>\nThe game was written using C# and Unity 3D Engine.\nI worked on all the major aspects of the fire level.\nThis included designing the map and implementing moving platforms, fire hazards, and environmental triggers.</p>\n<p><img src=\"/static/SourceFire.jpg\"></p>\n<p><img src=\"/static/SourceTrees.jpg\"></p>","frontmatter":{"date":"January 29, 2016","path":"/SourceGame","title":"Source","image":"/static/SourceGame.jpg"}}},"pathContext":{}}

